<template>
<div class='wrapper'>
 <v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
     <div id="infoform">
<el-form id="form1" :inline="true" :rules="rules" :model="form1" ref="form1" label-width="100px" >
    <el-row style="width:1400px">    
    <el-form-item label="姓名：" prop="s_name">
        <el-input v-model="form1.s_name" type="text" style="width:100%"></el-input>
     </el-form-item>
	</el-row>
	 <el-row style="width:1400px">    
    <el-form-item label="专业编号：" prop="smajorID">
        <el-input v-model="form1.smajorID" style="width:100%"></el-input>
     </el-form-item>
	 </el-row>
	 <el-row style="width:1400px">    
    <el-form-item label="班级：" prop="sclass">
        <el-input v-model="form1.sclass" style="width:100%"></el-input>
     </el-form-item>
	 </el-row>
	  <el-row style="width:1200px">    
         <el-form-item label="入学时间：" prop="stime_of_enrollment">
        <el-date-picker v-model="form1.stime_of_enrollment" type="date" style="width:100%"></el-date-picker>
     </el-form-item>
	 </el-row>

<el-col></el-col>
</el-form>
<el-form id="form2" :inline="true" label-width="150px" >
<el-form-item>
        <el-button type="primary" @click="sumitForm('form1')">保存</el-button>
                 <el-button type="success" @click="resetForm('form1')">重置</el-button>
                 <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
    
     </el-form>
        </div>
      </div>
    </div>
   </div>
</div>
</template>
    
<script>
 import vHead from '../commons/AdminstratorNav.vue';
export default {
  components:{
      vHead,
  },
  data(){
  
      return{
          hostUrl:'/student',
          check1: false,
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
          form1:{
              
              s_name: '',
              smajorID:'',
              sclass:'',
              stime_of_enrollment:'',
              
             
          },
		  rules: {
         
          s_name: [
            { required: true, message: '输入名字', trigger: 'blur' },
          ],
           smajorID: [
            { required: true, message: '请输入专业编号', trigger: 'blur' },
            { min: 12, max: 12, message: '长度为12个字符', trigger: 'blur' }
          ],
          sclass:[
               { required: true, message: '请输入学生班级', trigger: 'blur' }
          ],
         stime_of_enrollment:[
             { type: 'date', required: true, message: '请选择时间', trigger: 'change' },
         ]

        }
      };
  },
  methods:{
        sumitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      exit(formName) {
        this.$router.push({path: '/adminstrator/studentInfo/'})
      }
    }
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
}
#infoform{
    padding-top: 60px;
}
#form1{
    border:1px solid #eef1f6;
    width:400px;
    margin:10px auto;
}
#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>


